<template>
    <div class="d_container">
        <div class="d_content">
            <div class="b_title" v-if="$i18n.locale=='zh'"></div>
            <div class="b_title" v-else>Global Trade Compliance Statement</div>
            <div class="tline"></div>
            <!--  -->
            <!-- <p :style="{ whiteSpace: 'pre-wrap' }">{{ text }}</p> -->
            <div v-if="$i18n.locale=='zh'">
                <div v-html="zhtext"></div>
            </div>
            <div v-else><div v-html="entext"></div></div>
        </div>
        <div class="d_line"></div>
    </div>
</template>
<script>
import axios from "axios";
import { baseUrl } from '@/utils/env'
export default{
    components:{
    },
    data() {
        return{
            title:'',
            text:'',
            entext:`HONENG ELECTRONICS CO., LTD (hereinafter referred to as " HONENG ELEC.") 
HONENG ELEC. company policy is to fully comply with all relevant laws and regulations administered by the countries in which HONENG ELEC. conducts global trade. Under no circumstances will HONENG ELEC. sell or ship product contrary to applicable export control laws and trade regulations. Given the specific nature of the goods HONENG ELEC. sells, we make the following special statement: HONENG ELEC.s distributors, resellers, customers, and end-users are responsible for compliance with U.S. export control and import laws and regulations when exporting, re-exporting, importing, or otherwise transferring any HONENG ELEC. products or technology, including products derived from or based on such technology (collectively, HONENG ELEC. Products). These laws impose restrictions on the shipment or transfer of HONENG ELEC. Products to certain countries and persons, and/or on the ultimate end-uses of such products. It is you or your organizations responsibility to be aware of applicable export control and import restrictions and comply with them. HONENG ELEC. assumes no responsibility or liability for your and your organizations failure to comply. Failure to comply with U.S. and non-U.S. export control laws and trade regulations may result in civil and criminal penalties and any loss or other legal consequences arising from non-compliance will be solely the responsibility of you or your organization. If you or your organizations non-compliance causes any loss to HONENG ELEC., we will seek legal redress.

By purchasing, selling, using, or otherwise transferring HONENG ELEC. Products, you or your organization agree to comply with all applicable export and re-export control laws and regulations, including the U.S. Export Administration Regulations (EAR), administered by the U.S. Department of Commerces Bureau of Industry and Security (BIS), the International Traffic in Arms Regulations administered by the U.S. State Departments Directorate of Defense Trade Controls, and trade sanctions administered by the Treasury Departments Office of Foreign Assets Control (OFAC). You and your organization agree that you or your organization will not, directly or indirectly, without any required government authorization, sell, export, re-export, transfer, divert, or otherwise dispose of any HONENG ELEC. Products to:
1.Any country or territory that is not authorized to receive the HONENG ELEC. Products based on their applicable licensing jurisdiction and export control classification;
2.Any country or territory that is a designated state sponsor of terrorism or subject to comprehensive U.S. trade sanctions (including the Entity List, Specially Designated National List, or other applicable U.S. government sanction list, including, but not limited to, Crimea, Cuba, Iran, North Korea, Sudan, Syria, Russia, Belarus, and any additional country or region added in the future);
3.Any person or entity on a U.S. Government list of prohibited person and entity, including but not limited to, the Specially Designated Nationals and Blocked Persons List, Sectoral Sanctions Identifications List, and Foreign Sanctions Evaders List, administered by OFAC; and the Denied Persons List, Entity List, and Unverified List, administered by BIS;
4.Any person or entity with knowledge or reason to know that HONENG ELEC.s Products will be used for a prohibited end use, including activities related to the research, development, design, manufacture, construction, operation or maintenance of nuclear, chemical, or biological weapons; 
5.Any military end user or any person or entity with knowledge or reason to know that HONENG ELEC.s Products will be used for a military end use, as defined and subject to the license requirements in Section 744.21 of the EAR; or any military-intelligence end user, as defined and subject to the license requirements in Section 744.22 of the EAR

<span style="font-weight: bold; font-size: 18px;">Important announcement: </span>
<span style="font-weight: bold; font-size: 18px;">Please ensure you and your organization strictly comply with above statement. HONENG ELEC. reserves the right to immediately terminate any agreement or business relationship without prior notice in the event of any breach of this compliance statement.</span>`,

            zhtext:`()
 
                    
                    
                    1.	

                    2.	SDN

                    3.	

<span style="font-weight: bold; font-size: 18px;"></span>
<span style="font-weight: bold; font-size: 18px;"></span>`
        }
    },
    created() {
        this.getList()
    },
    methods:{
        getList() {
            this.zhtext = this.zhtext.replace(/\n/g,"<br/>")
            this.entext = this.entext.replace(/\n/g,"<br/>")
            const apiUrl = `${baseUrl}/api/agreement/list`;
            axios.get(apiUrl + "?language=" + (this.$i18n.locale=='zh'?'0':'1')).then(response => {
                if(response.data.code==200) {
                    response.data.rows.map(item=>{
                        // \n<br>
                        this.$set(item,'content',item.content?item.content.replace(/\n/g,"<br/>"):"")
                        this.$set(item,'contentEn',item.contentEn?item.contentEn.replace(/\n/g,"<br/>"):"")
                        if(this.$i18n.locale=='zh') {
                            if(item.id==1) {
                                this.text = item.content
                                this.title = item.title
                            }
                        } else {
                            if(item.id==1) {
                                this.text = item.contentEn
                                this.title = item.titleEn
                            }
                        }
                    })
                }
            })
            .catch(error => {
                
            });
        },
    }
}
</script>
<style lang="scss" scoped> 
.d_container{
    // background: #FAFAFA;
    .d_content{
        width: 1200px;
        margin: 0 auto;
        padding-bottom: 60px;
        .b_title{
            font-family: Arial;
            font-weight: bold;
            font-size: 30px;
            color: #333333;
            text-align: center;
            margin-top: 60px;
        }
        .tline{
            width: 100%;
            height: 1px;
            background-color: #ccc;
            margin: 20px 0;
        }
        .s_title{
            font-family: Arial;
            font-size: 18px;
            color: #333333;
            text-align: center;
            margin-bottom: 20px;
        }
        .d_txt{
            margin-bottom: 20px;
        }
        img {
            width: 1200px;
        }
    }
    .d_line{
        width: 100%;
        height: 20px;
        background: #FAFAFA;
    }
}

</style>