                },

            ],
            position: 0,
            speed: 1,
            intervalId: null

        }
    },
    beforeUnmount() {
        // this.stopScroll();
    },
    mounted() {
        // this.startScroll();
        // this.getList()
        // console.log(process.env.BASE_URL,"sdsds")
    },
    methods:{
        updateCarouselArrow() {
            if (this.carouselList.length === 1) {
                this.carouselArrow = 'never';
            } else {
                this.carouselArrow = 'always';
            }
        },
        swiperClick(item) {
            if(this.$i18n.locale=='zh') {
                if(item.url) {
                    window.open(`https://${item.url}`, '_blank');
                }
            } else {
                if(item.urlEn) {
                    window.open(`https://${item.urlEn}`, '_blank');
                }
            }
        },
        getList() {
            const apiUrl = `${baseUrl}/api/advert/list`;
            axios.get(apiUrl + "?language=" + (this.$i18n.locale=='zh'?'0':'1')).then(response => {
                this.carouselList = response.data.data
                this.updateCarouselArrow()
            })
            .catch(error => {
                
            });
        },
        checkMoreAboutUs() {
            this.$router.push({name:'aboutus'})
        },
        clickV() {
            this.$router.push({name:'vdetails'})
        },
        clickC() {
            this.$router.push({name:'cdetails'})
        },
        clickQ() {
            this.$router.push({name:'qdetails'})
        },
        clickW() {
            this.$router.push({name:'wdetails'})
        },
        startScroll() {
            if (this.intervalId) return;
            this.intervalId = setInterval(() => {
                this.position -= this.speed;
                if (this.$refs.imageWrapper && this.$refs.imageWrapper.offsetWidth) {
                    if (this.position <= -this.$refs.imageWrapper.offsetWidth / 2) {
                        this.position = 0;
                    }
                }
                
            }, 16);
        },
        stopScroll() {
            clearInterval(this.intervalId);
            this.intervalId = null;
        }
    }
}
</script>
